import React from 'react'

export default class ListOfUsers extends React.Component {
    render() {
        const {item} = this.props
        return (
          <div className="list-group">
              <h1 className="list-group-item">
                  <a href="#" className="list-group-item list-group-item-action flex-column align-items-start active">
                      <div className="d-flex w-100 justify-content-between">
                          <h5 className="mb-1">Username: {item['username']}</h5>
                          <small> ID: {item['id']}</small>
                      </div>
                      <p className="mb-1">  First Name is: {item['first_name']} , Second Name is: {item['last_name']}. <br/> </p>
                       <p className="text-decoration-underline ">   {item['is_superuser'] === true ? "And this person is superuser" : "Unfortunately this person isn`t superuser"} </p>
                      <small>Account active : {item['is_active'] === true ? "YES" : "NO"}</small>
                  </a>
              </h1>
          </div>
        );
    }

}



