import React from 'react'

const linkAuth = "https://emphasoft-test-assignment.herokuapp.com/api-token-auth/";

// for connection "http://emphasoft-test-assignment.herokuapp.com/api-token-auth/" method="POST"\
/*fetch("http://emphasoft-test-assignment.herokuapp.com/api-token-auth/", {
    method: "POST",
    mode:"cors",
    headers: {
        "Content-type": "application/json"
    },
    body: JSON.stringify({
        username : "test_super",
        password: "Nf<U4f<rDbtDxAPn",
    })
})*/
    let userData = {username: "", token: ""}

export default class LoginForm extends React.Component {
    state = {
        username: "",
        password: "",
        errors: {},
        submitting: false,
    };

    onChange = e => {
        const name = e.target.name
        const value = e.target.value
        this.setState(prevState =>({
            [name]: value,
            errors : {
                ...prevState.errors,
                [name]: null,
                base: null
            }
        }));
    }
    handleBlur = () =>{
        const errors = this.validateFields();
        if (Object.keys(errors).length > 0){
            this.setState(prevState =>({
                errors : {
                    ...prevState.errors,
                    ...errors
                }
            }));
        }
    }

    validateFields = () => {
        const errors = {};

        const usernameRegexp = new RegExp("^[\\w.@+-]+$");
        const passwordRegexp = new RegExp("^(?=.*[A-Z])(?=.*\\d).{8,}$");
        if (this.state.username === ""){
            errors.username = "Cann`t be empty"
        }
        if (this.state.password === ""){
            errors.password = "Cann`t be empty"
        }
        if (this.state.username && !usernameRegexp.test(this.state.username.toString())){
            errors.username = "Should contain: A-z and '@ + - _' "
        }
        if (this.state.password && !passwordRegexp.test(this.state.password.toString())){
            errors.password = "Should contain uppercase and lowercase letters, number. And must be between 8 and 150 letters"
        }
        if(this.state.username.length > 150){
            errors.username = "Max 150 symbols"
        }
        if(this.state.password.length > 150){
            errors.password = "Max 150 symbols"
        }

        return errors;
    }

    onSubmit = async () => {
        const fetchApi = (url, options = {}) => {
            return new Promise((resolve, reject) => {
                fetch(url, options)
                    .then(response => {
                        if (response.status < 400) {
                            this.setState({
                                errors: {
                                    base: null
                                }
                            })
                            return response.json();
                        } else if (response.status === 400) {
                            this.setState({
                                errors: {
                                    base: "Enter the correct username / password"
                                }
                            })
                        } else {

                            throw response;

                        }
                    })
                    .then(data => {
                        resolve(data);
                    })
                    .catch(response => {
                        response.json().then(error => {
                            reject(error);
                        })
                    })

            })

        }
        this.setState({
            submitting: true
        });

        const data = await fetchApi(linkAuth, {
            method: "POST",
            mode: "cors",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify({
                username: this.state.username,
                password: this.state.password,
            })
        }).then(data => {
            if (data){
                userData.username = this.state.username
                userData.token = data["token"]
            }
            this.props.updateData(userData)
            this.setState({
                submitting: false
            });

        })
    }

    //How to get list of users
    /*  fetch(linkGetList, {
             method: "GET",
             headers: {
                 "Authorization": `Token ${dataToken}`
             }
         }).then(response => {
             idList = response;
         })*/

    onLogin = e =>{
        e.preventDefault();
        const errors = this.validateFields();

        if (Object.keys(errors).length > 0){
            this.setState(prevState =>({
                errors : {
                    ...prevState.errors,
                    ...errors
                }
            }));
        } else {
            this.onSubmit();
        }
    }

    render() {
        const {username, password, errors, submitting} = this.state;
        return (
            <div className="form-login-container ">
                <form className="form-login ">
                    <h1 className="h3 mb-3 font-weight-normal text-center">
                        Authorization
                    </h1>
                    <div className="form-group mt-2">
                        <label htmlFor="username">User</label>
                        <input
                            type="text"
                            className="form-control mt-1"
                            id = "username"
                            placeholder="User name"
                            name = "username"
                            value={username}
                            onChange={this.onChange}
                            onBlur={this.handleBlur}
                        />
                        <div className="invalid-form"> {errors.username} </div>
                    </div>
                    <div className="form-group mt-2">
                        <label htmlFor="password">Password</label>
                        <input
                            type="password"
                            className="form-control mt-1"
                            id = "password"
                            placeholder="Password"
                            name = "password"
                            value={password}
                            onChange={this.onChange}
                            onBlur={this.handleBlur}
                        />
                        <div className="invalid-form"> {errors.password} </div>
                    </div>

                    <button
                        type = "submit"
                        className="btn btn-lg btn-primary btn-block mt-3 "
                        onClick={this.onLogin}
                        disabled={submitting}
                    >Sing in </button>
                    <div className="invalid-form"> {errors.base} </div>
                </form>

            </div>


        );
    }

}



