import React,{Component} from 'react'
import './App.css';
import { Modal, ModalBody } from 'reactstrap';
import LoginForm from "./Login/LoginForm";
import ListOfUsers from "./List/ListOfUsers";

const linkGetList = "https://emphasoft-test-assignment.herokuapp.com/api/v1/users/"


class App extends Component{
  constructor(props) {
    super(props);
    this.state = {
      showModal: false,
      userData : {},
      switchAuth: false,
      usersList: {},
      showList: false,
    }
  }

  //How to get list of users
  /*  fetch(linkGetList, {
           method: "GET",
           headers: {
               "Authorization": `Token ${dataToken}`
           }
       }).then(response => {
           idList = response;
       })*/


  // Получаем лист через промис
  getList = async () => {
    const dataToken = this.state.userData.token;
    const fetchApi = (url, options = {}) =>{
      return new Promise((resolve, reject) =>{
        fetch(url,options)
            .then(response =>{
              if (response.status < 400){
                return response.json()
              }else{
                throw response;
              }
            })
            .then(data => {
              resolve(data)
            })
            .catch(response =>{
              response.json().then(error =>{
                reject(error);
              })
            })
      })
    }
    //Отправляем GET запрос на сервер вместе с токеном
    const userList = await fetchApi(linkGetList, {
      method: "GET",
      headers: {
        "Authorization": `Token ${dataToken}`
      }
    })
        // Записываем данные и включаем тригер для показа листа
        .then(data => {
          this.setState({
            usersList: data,
            showList: true
          })
        })
  }



  // получаем данные формы
  updateData = userData =>{
    if (userData.username !== "" && userData.token !== ""){
      this.setState({
        userData: userData,
        switchAuth: true,
      })
      if (userData){
        this.toggleModal();
      }
    }
  }

  //Переключатель видмости окна авторизации
  toggleModal = () =>{
    this.setState(prevState =>({
      showModal: !prevState.showModal
    }));
  }

  //Сортировка по возрастанию
  sortListAZ = () => {
    this.setState({
      usersList: this.state.usersList.sort((a, b) => a.id > b.id ? 1 : -1)
    })

  }

  //Сортировка по убыванию
  sortListZA = () => {
    this.setState({
      usersList: this.state.usersList.sort((a, b) => a.id < b.id ? 1 : -1)
    })

  }

  //Фильрация по UserName
  filterUsername = () => {
    const name = document.getElementById('filter').value
    this.state.usersList.map(user =>{
      if(!user['username'].toLocaleLowerCase().includes(name)){
        document.getElementById(user['username']).classList.add('d-none')
      } else {
        document.getElementById(user['username']).classList.remove('d-none')
        document.getElementById(user['username']).classList.remove('d-none')
      }
    })
  }

  render() {
    const {usersList} = this.state;
    return (
        <div className="container d-block">
          <div className="container d-flex justify-content-center mt-5 ">
            {!this.state.switchAuth ? <button className="btn btn-primary btn-block w-50" type="submit" onClick={this.toggleModal}>
              Login
            </button> : <p className="text-dark" >  You are login as : {this.state.userData.username} </p> }
            <Modal isOpen={this.state.showModal} toggle={this.toggleModal}>
              <ModalBody>
                <LoginForm updateData = {this.updateData}/>
              </ModalBody>
            </Modal>
          </div>
          <div className="container d-flex justify-content-center mt-5 mb-3">
            {this.state.switchAuth && <button
                className="btn btn-primary btn-lg btn-dark "
                onClick={this.getList}>
              Get list of users
            </button>}
          </div>
          <div className="container d-flex  justify-content-around">
            {/* Кнопка фильтрации по возрастанию */}
            {this.state.showList && <button
                className="btn btn-secondary "
                onClick={this.sortListAZ}>
              Sort: <br/>Low to High
            </button>
            }
            {/* Кнопка фильрации по убыванию */}
            {this.state.showList && <button
                className="btn btn-secondary w-auto"
                onClick={this.sortListZA}>
              Sort: <br/>High to Low
            </button>
            }
            {/* Поле для ввода фильтраци по UserName*/}
            {this.state.showList && <input
                type="text"
                className="form-text"
                onChange={this.filterUsername}
                id="filter"
                placeholder="Filter of username"
            />
            }
          </div>  {/* Проходимся по массиву полученному от бэкенда */}
          <div className="list-group">
            {this.state.showList && usersList.map(list =>{
              return (
                  <div id={list['username']} key={list['id']} className="list-group-item">
                    <ListOfUsers item={list}/>
                  </div>
              )
            })}
          </div>
        </div>

    );
  }

}

export default App;




